      h2 3.1 Procedimiento del proceso de elaboracin de preparados magistrales

    p.mb-5(data-aos="fade-right") Como todo proceso, las preparaciones magistrales deben tener como base un manual o procedimiento y los respectivos documentos que se generan de estos (instructivos, formatos), donde se detalle la siguiente informacin:

