                    i.fas.fa-link(style="color: #000")
    
    p.mb-5(data-aos="fade-right") En el siguiente flujograma se muestran las etapas del proceso de elaboracin de los preparados magistrales, desde la prescripcin mdica, pasando por las verificaciones de las condiciones locativas, para garantizar la pertinencia con las BPE, controles de calidad, acondicionamiento y dispensacin al usuario final. 

    .row.justify-content-center.mb-5
