        h5 Figura 7.
        span Flujograma proceso fabricacin preparaciones magistrales
      figure(data-aos="zoom-in")
        img(src="@/assets/curso/tema3/img06.svg" alt="Imagen que muestra a travs de un esquema las diferentes etapas del proceso de elaboracin de los preparados magistrales, que inicia desde la prescripcin que hace el medico tratante, hasta la dispensacin al paciente o usuario final.")
    
    p(data-aos="fade-right") En este recorrido por el proceso de elaboracin de las preparaciones se tratarn los siguientes temas:
    ul.lista-ul--color
      li(data-aos="fade-right")
        i.fas.fa-atom(style="color: #FF9D00;")
        |
