    separador
    #t_3_2.titulo-segundo.color-acento-botones(data-aos="fade-right")
      h2 3.2 Requisitos rea de recepcin y almacenamiento de las preparaciones magistrales

    .BGIMG05.px-md-5.px-4.py-4.mb-4
      p.mb-5(data-aos="fade-right") El rea para la recepcin de materias primas y materiales de acondicionamiento debe ser independiente del rea de fabricacin que debe estar identificada y debe contar con las zonas delimitadas de ingresos, cuarentena y material rechazado. 
      .row.justify-content-center
