    p.mb-5(data-aos="fade-right") Los requisitos del rea de recepcin y almacenamiento de materias primas son:

    .row.justify-content-start.mb-5
      .col-xl-4.col-md-6.mb-4(data-aos="fade-right")
        .tarjeta-numerada.color-acento-contenido.h-100.ps-5.pe-4.py-4
          .tarjeta-numerada__numero
            .h2 1
          p.mb-0 El rea de almacenamiento de materias primas y material de acondicionamiento debe ser de acceso restringido para personas ajenas al establecimiento; asimismo, debe ser de fcil limpieza, equipado con mobiliario adecuado para este fin, con una separacin mnima de 20 cm del suelo y del techo que facilite su limpieza y garantice la proteccin del polvo y la luz (si aplica).
      .col-xl-4.col-md-6.mb-4(data-aos="fade-up")
        .tarjeta-numerada.color-acento-contenido.h-100.ps-5.pe-4.py-4
          .tarjeta-numerada__numero
            .h2 2
          p.mb-0 Debe contar con un refrigerador para los productos que requieran refrigeracin que incluya un termmetro y los registros de temperatura correspondientes, con sus lmites de alerta y de accin. 
      .col-xl-4.col-md-6.mb-4(data-aos="fade-left")
        .tarjeta-numerada.color-acento-contenido.h-100.ps-5.pe-4.py-4
          .tarjeta-numerada__numero
            .h2 3
          p.mb-0 Las reas destinadas para el almacenamiento de especialidades farmacuticas debern estar separadas y ser completamente independientes. 
      .col-xl-4.col-md-6.mb-4(data-aos="fade-right")
        .tarjeta-numerada.color-acento-contenido.h-100.ps-5.pe-4.py-4
          .tarjeta-numerada__numero
            .h2 4
          p.mb-0 El mobiliario para el resguardo del material de acondicionamiento debe ser adecuado para evitar su contaminacin y que se ponga directamente en el piso. 
      .col-xl-4.col-md-6.mb-4(data-aos="fade-up")
        .tarjeta-numerada.color-acento-contenido.h-100.ps-5.pe-4.py-4
          .tarjeta-numerada__numero
            .h2 5
          p.mb-0 Las etiquetas deben estar almacenadas en un lugar protegido de la humedad y de acceso controlado. 

