      .col-lg-4.px-0.order-lg-2.order-3(data-aos="fade-up")
        .BG05.p-4
          p En este paquete tan importante dentro del proceso de elaboracin de los productos farmacuticos, es donde se dejan evidencias de todas las etapas necesarias, se consignan registros como: 
          ul.lista-ul--color
            li
              i.fas.fa-atom(style="color: #FF9D00;")
              | Orden de produccin.
            li
              i.fas.fa-atom(style="color: #FF9D00;")
              | Despeje de lnea inicial.
            li
              i.fas.fa-atom(style="color: #FF9D00;")
              | Procedimiento de elaboracin.
            li
              i.fas.fa-atom(style="color: #FF9D00;")
              | Producto en proceso.
            li
              i.fas.fa-atom(style="color: #FF9D00;")
              | Despeje de lnea final.
            li
              i.fas.fa-atom(style="color: #FF9D00;")
              | Lista de chequeo de proceso de llenado.
            li
              i.fas.fa-atom(style="color: #FF9D00;")
              | Limpieza de equipos.
      .col-lg-4.col-6.ps-0.order-lg-3.order-2.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema3/img09.jpg' alt="Imagen decorativa")

    p(data-aos="fade-right") Cada uno de los lotes que se produzcan de un medicamento, debe tener su propio #[i Batch Record] y ser almacenados y controlados por el director tcnico responsable del servicio o establecimiento farmacutico. 
    p.mb-5(data-aos="fade-right") En tal sentido, a continuacin, se encuentran las definiciones, requisitos y responsables de este proceso:

    .BG11.p-4.mb-5
      SlyderA(tipo="b" data-aos="zoom-in")
        .row.justify-content-center
          .col-lg-6.order-lg-1.order-2
            .h4 Definiciones
            ul.lista-ul--color
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong #[i Batch Record] (BR):] registro que contiene la informacin sobre la produccin y el control ejercido en las diferentes etapas de fabricacin o acondicionamiento de un producto.
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong Liberacin del lote:] aprobar y autorizar el lote para su distribucin.
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong Lote origen (LO):] lote origen (del material) o asignado por el cliente.
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong Lote interno (LI):] lote asignado por produccin y gestin.
          .col-lg-4.col-8order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema3/img10.png' alt="Imagen decorativa")
        .row.justify-content-center
          .col-lg-6.order-lg-1.order-2
            .h4 Requisitos
            ul.lista-ul--color
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                |
                p.mb-0 El #[i Batch Record] debe ser diligenciado por el personal encargado de cada proceso.
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                |
                p.mb-0 La informacin debe ser legible, sin enmendaduras y no se deben dejar espacios en blanco. 
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                |
                p.mb-0 Diligenciar cada campo del #[i Batch Record] en el momento en que se est realizando el proceso indicado. Los documentos deben llenarse con esfero de tinta negra.
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                |
                p.mb-0 Cuando varias actividades que se ejecuten en una misma hoja del #[i Batch Record] ocurren simultneamente en 2 o ms procesos. Se debe contar con una copia de esta en cada proceso que se adjuntar al #[i Batch Record].
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                |
                p.mb-0 Cuando los campos disponibles para diligenciar se agoten en alguna de las hojas. Se debe diligenciar una copia en blanco de la misma hoja. 
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                |
                p.mb-0 El personal encargado debe solicitar a su jefe inmediato el #[i Batch Record] para diligenciar inmediatamente le sea entregada la orden de produccin.
          .col-lg-4.col-8order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema3/img11.png' alt="Imagen decorativa")
        .row.justify-content-center
