            ul.lista-ul--color
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong Personal operativo:] consignar la informacin completa y correcta, cumpliendo con las buenas prcticas de registro. La ltima persona encargada del proceso al que es sometido un producto deber recopilar y entregar el #[i Batch Record] a ingeniera de procesos o supervisin de produccin.
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong Supervisin de produccin:] debe asegurar que la informacin consignada en el #[i Batch Record] est completa, sea verdica y cumpla con las buenas prcticas de registro, antes de entregarlo al proceso de control de calidad.
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong Coordinacin de planeacin:] suministrar las rdenes de produccin con la informacin necesaria para el diligenciamiento bsico del #[i Batch Record].
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong Ingeniera de procesos:] asegurar que la informacin consignada en el #[i Batch Record] est completa, verificada y que cumpla con las buenas prcticas de registro, antes de entregarlo al proceso de control de calidad. 
              li
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[strong Direccin tcnica:] revisar y aprobar los #[i Batch Record] para la liberacin de productos salvaguardados fsicamente y compilar su informacin digitalmente, asegurndose de que se cumplan todas las normas estipuladas. 
          .col-lg-4.col-8order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema3/img12.png' alt="Imagen decorativa")
    
    p.mb-5(data-aos="fade-right") Los documentos consignados en el #[i Batch Record], deben guardar un orden lgico relacionado con el proceso productivo. Es importante conocer cada uno de ellos, su estructura y funcin:
    
    .BG06.p-4.mb-5(data-aos="zoom-in")
      LineaTiempoC.color-secundario
        .row.justify-content-center.p-4
          .col-lg-5.mb-lg-0.mb-4
            .h4 Orden de produccin
            p Antes de iniciar con el proceso de produccin y diligenciamiento del #[i Batch Record], se debe tener como insumo la informacin relacionada con el producto, la cual se detalla en la orden de produccin que se genere y que autoriza el inicio de dicha actividad.
            p.mb-0 La informacin que debe ir en este documento es:
          .col-lg-5.col-10.BG12.p-4
            .h4.text-center Procedimiento 
            p Consecutivo <a style="color: #00BCD4">No. 00000001</a> (programa de produccin)
            p Se debe diligenciar:
            ul.lista-ul--color
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Fecha de inicio.
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Fecha de finalizacin (al terminar la produccin).
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Fecha de vencimiento del producto.
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Orden de produccin del sistema de manejo de inventario (Factory).
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                p.mb-0 #[em Stock] del producto a realizar.
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Descripcin del producto.
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Cantidad programada.
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Rendimiento terico.
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Rendimiento real (al terminar la produccin).
              li(style="margin-bottom: 2px")
                i.fas.fa-atom(style="color: #7E5EE3;")
                | Lote.
        .row.justify-content-center.p-4
          .col-lg-5.mb-lg-0.mb-4
            .h4 Asignacin lote
            p Cada servicio o establecimiento farmacutico tendr su propio mtodo para la determinacin y codificacin del lote asignado a cada producto elaborado y que lo identificar en todo momento durante la produccin de este y su posterior almacenamiento y dispensacin al usuario final.
          .col-lg-7.col.BG12.p-4
            p La primera letra corresponde al ao de produccin del producto que se relaciona con la palabra #[em #[strong complaints]], donde cada letra equivale a un ao de la dcada como se muestra a continuacin:
            table.mb-4
              thead
                tr
