                  th(style="padding: 0.3em; border-top: 3px solid #7E5EE3; border-right: 3px solid #7E5EE3; font-size: 18px").text-center O
                  th(style="padding: 0.3em; border-top: 3px solid #7E5EE3; border-right: 3px solid #7E5EE3; font-size: 18px").text-center M
                  th(style="padding: 0.3em; border-top: 3px solid #7E5EE3; border-right: 3px solid #7E5EE3; font-size: 18px").text-center P 
