                  th(style="padding: 0.3em; border-top: 3px solid #7E5EE3; border-right: 3px solid #7E5EE3; font-size: 18px").text-center I 
                  th(style="padding: 0.3em; border-top: 3px solid #7E5EE3; border-right: 3px solid #7E5EE3; font-size: 18px").text-center N
                  th(style="padding: 0.3em; border-top: 3px solid #7E5EE3; border-right: 3px solid #7E5EE3; font-size: 18px").text-center T
