            .h4 Despeje de lnea
            p Esta actividad tiene como objetivo asegurar que el producto y los materiales a intervenir en el proceso de fabricacin han sido verificados correctamente y que no hay posibilidad de mezcla con productos diferentes, o lotes diferentes anteriormente trabajados en lnea.
            p Como se puede observar en la tabla, se verifica que no hay presencia de ningn material del producto o documento elaborado anteriormente, que las reas estn despejadas y limpias. Luego, se verifica que la informacin material del lote a elaborar, al igual que las reas, cumplan con todos los requisitos para iniciar el nuevo proceso. 
            p.mb-0 En ambos casos se deja la firma de quien realiza la operacin y quien la verifica.
