            .h4 Controles proceso
            p Durante todas las etapas de elaboracin del producto se deben realizar controles a medida que este avanza, para garantizar que dicha actividad se lleva a cabo en condiciones adecuadas y prever cualquier no conformidad que surja. Dichos controles se realizan durante las etapas de llenado y acondicionamiento del producto. 
            p.mb-0 Como modelos de formatos para registrar dicha informacin, se tienen los representados en las tablas.
          .col-lg-7.col.BG12.p-4
            .titulo-sexto.color-acento-botones(data-aos="fade-right")
              h5 Tabla 7.
              span Informacin para envasado (volumen y densidad)
