            .h4 Controles proceso acondicionamiento
            p En el ejemplo de las tablas, se observan los controles al proceso de acondicionamiento verificando que cumpla con las siguientes caractersticas: 
            ul.lista-ul--color
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                |
                p.mb-0 Etiqueta correcta y bien situada.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                |
                p.mb-0 #[i Sticker] colocado, si se encuentra bien sellado.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                |
                p.mb-0 Limpieza exterior.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                |
                p.mb-0 Presencia del lote y la codificacin.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                |
                p.mb-0 Se observan los intervalos en los cuales se hacen los controles y el reporte del cumplimiento con la letra C.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                |
                p.mb-0 Firma de quien hizo la verificacin.
