        .row.justify-content-center.p-4
          .col-lg-5.mb-lg-0.mb-4
            .h4 Controles producto entregado
            ul.lista-ul--color
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                | En diferentes fases del proceso de produccin, se pueden generar novedades o solicitudes de producto, ya sea que este se encuentre sin procesar, en proceso, terminado, cuarentena o rechazado. Estos movimientos deben ser controlados y monitoreados para conocer el estado actual del producto, controlar el inventario de este, conocer su ubicacin y la causa de la decisin de traslado.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                | Un ejemplo claro podra ser la elaboracin de un preparado magistral que est en fase de cuarentena, de este producto se deben sacar muestras y enviar a control de calidad para que realice las pruebas pertinentes con el objetivo de verificar si cumple con los parmetros establecidos. Dicho movimiento de las muestras debe quedar registrado en el formato designado para esto.
          .col-lg-5.col-10.BG12.p-4
