              h5 Tabla 9.
              span Controles producto entregado
            figure.indicador__container(@click="modal3 = true")
              img(src='@/assets/curso/tema3/img16.svg' alt="Tabla que muestra la estructura del formato llevar el control del producto entregado.")
              .indicador--click(v-if="mostrarIndicador")
        .row.justify-content-center.p-4
          .col-lg-5.mb-lg-0.mb-4
            .h4 Conciliacin materiales
            p Luego del proceso de aprobacin por parte del rea de control de calidad, se procede a la realizacin del proceso de acondicionamiento final donde se realizan controles externos al producto como: 
            ul.lista-ul--color
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                | Errores de impresin en la etiqueta.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                | Rayones.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                | Quebrados rotos.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                | Dimensiones errneas.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                | Contaminacin.
              li
                i.fas.fa-atom(style="color: #FF9D00;")
                | Golpes, entre otros.
            p.mb-0 La tabla muestra un ejemplo de la informacin a controlar.
          .col-lg-5.col-10.BG12.p-4
            .titulo-sexto.color-acento-botones
              h5 Tabla 10.
              span Control de informacin
            figure.indicador__container(@click="modal4 = true")
              img(src='@/assets/curso/tema3/img17.svg' alt="Tabla que muestra los procesos sobre el control de la informacin.")
              .indicador--click(v-if="mostrarIndicador")
        .row.justify-content-center.p-4
          .col-lg-5.mb-lg-0.mb-4
            .h4 Formato aprobacin
            p Finalmente, el director tcnico responsable, deber revisar cada uno de los documentos del #[i Batch Record], verificar que cumplieron con todos los requisitos y dar su aval para que se libere el lote y pase al proceso de almacenamiento para su posterior dispensacin al usuario final.
            p.mb-0 #[strong Nota]: si durante el proceso existen algunas desviaciones de calidad, que no afectan al producto ni al usuario final, el director tcnico debe dejar aclarado eso en el #[i Batch Record].
          .col-lg-5.col-10.BG12.p-4
