    ModalA.BS(:abrir-modal.sync="modal5")
      .h4 Balanzas de precisin que pesen desde 1 mg a 2 kg
      p.mb-0 Disponen de un plato que puede ir protegido por paredes de vidrio, sobre este se pone el objeto a pesar en el interior del recipiente adecuado. El peso aparecer en la pantalla digital. 
    ModalA.BS(:abrir-modal.sync="modal6")
      .h4 Bao termosttico. De forma cuadrada o rectangular
      p.mb-0 Llevan en su interior una resistencia elctrica que calienta el agua hasta una temperatura prefijada por un termostato. Pueden disponer de algn mecanismo que remueva el agua para que el calentamiento sea homogneo.
    ModalA.BS(:abrir-modal.sync="modal7")
      .h4 Morteros de vidrio y porcelana
      p.mb-0 Tienen forma de cuenco y cuentan con un pico en el borde para el desalojo de su contenido. La mano o pistilo sirven para pulverizar, realizar mezclas, homogeneizar y amasar.
    ModalA.BS(:abrir-modal.sync="modal8")
      .h4 Auxiliares de pipeteo y varillas
      p.mb-0 Los pipeteadores o auxiliares de pipeteo se utilizan acoplados a las pipetas manuales. Si sirven de un mbolo para realizar la aspiracin de los lquidos. Las varillas de vidrio para agitar son tubos de vidrio macizos de diferentes tamaos; se utilizan para realizar mezclas.
    ModalA.BS(:abrir-modal.sync="modal9")
      .h4 Agitadores magnticos y de hlice
      p.mb-0 Sirven para acelerar la mezcla de los componentes de una disolucin. En los agitadores magnticos se introducen imanes en las disoluciones para que se mezclen correctamente los componentes.
    ModalA.BS(:abrir-modal.sync="modal10")
      .h4 Placas calefactoras
      p.mb-0 Superficie que proporciona calor seco constante para concentrar las disoluciones, evaporar disolventes o simplemente alcanzar una temperatura adecuada para un procedimiento de elaboracin determinado. Pueden llevar agitacin incorporada. En la imagen se ve una placa calefactores con agitacin.
    ModalA.BS(:abrir-modal.sync="modal11")
      .h4 Esptulas de metal y de goma
      p.mb-0 Su finalidad es separar fracciones de slido o recoger distintos tipos de productos acabados en formulacin magistral.
    ModalA.BS(:abrir-modal.sync="modal12")
      .h4 pH-metro
      p.mb-0 Sirve para medir la concentracin de hidrogeniones de una solucin. El pH-metro mide la diferencia de potencial que originan los iones hidrgeno (H+) de la solucin problema con respecto a la solucin de referencia.
    ModalA.BS(:abrir-modal.sync="modal13")
      .h4 Lente de aumento
      p.mb-0 Sirve para aumentar el tamao de la imagen de la muestra que se quiere estudiar. Una vez colocada est debajo de la lente, se mira a travs de los oculares para apreciar el aumento del tamao de esta y detalles que a simple vista pasan desapercibidos.
    ModalA.BS(:abrir-modal.sync="modal14")
      .h4 Termmetros
      p.mb-0 Tubo capilar en cuya parte inferior se encuentra un depsito lleno de mercurio. Al calentarse, el mercurio asciende por el capilar marcando la temperatura.
    ModalA.BS(:abrir-modal.sync="modal15")
      .h4 Sistema para determinar el punto de fusin (PF)
      p.mb-0 El ensayo de PF est muy extendido en los laboratorios. Se utiliza para identificar materias primas pulverulentas y para conocer su grado de pureza, pues en una sustancia pura el cambio de estado de slido a lquido es rpido y la temperatura permanece constante durante todo el proceso.
    ModalA.BS(:abrir-modal.sync="modal16")
      .h4 Alcohmetro
      p.mb-0 Dispositivo de laboratorio, semejante a un densmetro, graduado en grados alcohlicos que indica de forma cuantitativa la riqueza en alcohol de un lquido o disolucin.

    ModalA.BS(:abrir-modal.sync="modal17")
      .h4 Pipetas graduadas
      p.mb-0 Son tubos huecos de vidrio, cuya finalidad es trasvasar volmenes exactos de un recipiente a otro. Disponen de una graduacin que indica los volmenes que podemos recoger. Se utilizan con auxiliares de pipeteo. 
    ModalA.BS(:abrir-modal.sync="modal18")
      .h4 Pipetas automticas y puntas para volmenes de ml
      p.mb-0 Dispone de un mbolo que se manipula con el dedo pulgar, el cual permite que el lquido sea aspirado o expulsado. En la parte final de la pipeta automtica, se pone una punta de plstico que se introduce en el lquido para aspirarlo. Despus de la expulsin de la muestra lquida, la punta se desecha.
    ModalA.BS(:abrir-modal.sync="modal19")
      .h4 Probetas
      p.mb-0 Recipientes graduados en forma de tubo y con distintas capacidades. Llevan una base de apoyo y un pico en el borde que facilitan el vertido del lquido. Se utilizan para medir volmenes con un grado de aproximacin bastante alto al volumen exacto.
    ModalA.BS(:abrir-modal.sync="modal20")
      .h4 Matraces aforados
      p.mb-0 Recipientes de distintos volmenes con cuello estrecho y cuerpo ancho. En el cuello tienen la seal de aforo que indica dnde debe llegar la parte inferior del menisco que forma el lquido con las paredes del cuello. Se utiliza para preparar disoluciones de concentraciones perfectamente conocidas. 

    ModalA.BS(:abrir-modal.sync="modal21")
      .h4 Vasos de precipitados
      p.mb-0 Recipientes de boca y cuerpo ancho. En su pared aparece una escala graduada aproximada, ya que no se usa para realizar mediciones exactas, sino para distintas operaciones de laboratorio cmo preparar disoluciones, contener productos lquidos, etc.
    ModalA.BS(:abrir-modal.sync="modal22")
      .h4 Embudos
      p.mb-0 De distintos tamaos, forma cnica y pico largo y biselado o corto segn se utilicen para lquidos o polvos, respectivamente.
    ModalA.BS(:abrir-modal.sync="modal23")
      .h4 Matraces Erlenmeyer
      p.mb-0 Con fondo plano y ancho, cuello corto y distintas capacidades. Sirven para diluir reactivos y preservar las salpicaduras procedentes de reacciones qumicas que se produzcan en su interior.
    ModalA.BS(:abrir-modal.sync="modal24")
      .h4 Vidrio de reloj y pesasustancias
      p.mb-0 Pueden ser de vidrio o plstico. Se utilizan para pesar slidos y pueden tener diferentes tamaos. 
    ModalA.BS(:abrir-modal.sync="modal25")
      .h4 Tamices para polvo fino, grueso y muy grueso
      p.mb-0 Formados por un bastidor hay que se sujeta una serie de hilos entrecruzados de acero, nylon o bronce. Los tamices se clasifican por la abertura de malla, que no es ms que la distancia entre dos hilos continuos.

